.searchInput {
    width: 45px;
    margin-left: 17em;
    display: flex;
    text-align: center;
    border-radius: 50%;
    color: #000000;
}

.searchButton:hover {
    opacity: 0.5;
}

.searchButton {
    display: inline-block;
    border-radius: 4px;
    background-color: #5beb58;
    border: none;
    color: #181818;
    text-align: center;
    font-size: 20px;
    padding: 20px;
    width: 200px;
    transition: all 0.5s;
    margin: 5px;
}


.searchButton:hover {
    padding-right: 55px;
    opacity: 0.5;
}

h1 {
    font-size: 60px;
    text-shadow: -1px -1px #0c0, 1px 1px #060, -3px 0 4px #000;
    font-family: Arial, Helvetica, sans-serif;
    color: #090;
    padding: 16px;
    font-weight: lighter;
    text-align: center;
    display: block;
    margin: 16px;
}

h2 {
    font-size: 25px;
    text-shadow: -1px -1px #0c0, 1px 1px #060, -3px 0 4px #000;
    font-family: Arial, Helvetica, sans-serif;
    color: #090;
    padding: 16px;
    font-weight: lighter;
    text-align: center;
    display: block;
    margin: 16px;
}